
input[type="file"] {
   display: none;
}
.line-title {
   width: 100%;
   height: 70px;
   line-height: 70px;
   font-size: 22px;
}
.el-row {
   margin-bottom: 10px;
&:last-child {
     margin-bottom: 0;
}
}
 /* .el-form-item{
     margin-bottom: 25px;
 } */
.div_center {
   text-align: center;
   width:100%;
   margin:0 auto;
   margin-bottom: 20px;
}
.phoneNameBox{
   width:100%;
   border:1px solid #b3d8ff;
   min-height: 90px;
   border-radius:5px;
}
.phoneNameText{
   padding:0 10px;
   height:35px;
   line-height: 36px;
   margin-bottom: 0px;
}
.phoneNameText+.phoneNameText{
   border-top:1px solid #b3d8ff;
}
.phoneNameText:nth-child(2n){
    background: #ecf5ff;
}
.uploadTxtCallback{
   font-size: 12px;
   line-height: 18px;
   margin-top: 10px;
}
.iconButton{
   font-size:24px;
   position: relative;
   top:3px;
   cursor:pointer;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  margin-top: 10px;
}
